<template>
<div id="ubscribe">
	<van-nav-bar
		title="添加机器"
		left-text="返回"
		right-text="按钮"
		left-arrow
		@click-right="onClickRight"
		@click-left="onClickLeft"
		class="nav"
	/>
	<div class="van-cell-box">
		<van-cell-group :style="{background:'#f1f1f1'}">
			<div class="top10box">
			<van-cell title="购入日期" :value="date" @click="show = true"  />
			<van-calendar v-model="show" @confirm="onConfirm" />

			</div>
			<div class="top10box">
			<van-field v-model="machineObj['machinename']" label="机器名称" placeholder="请输入机器名称" />
				<van-field v-model="machineObj['machinenum']" label="机器编号" placeholder="请输机器编号" />
				
				<van-field name="machineObj.isuse" label="出品质量">
					<template #input>
						<van-rate v-model="machineObj.stars" />
					</template>
				</van-field>
				<van-field name="machineObj.isuse" label="状态">
					<template #input>
						<van-radio-group v-model="machineObj.isuse" direction="horizontal">
						<van-radio name="0">停用</van-radio>
						<van-radio name="1">启用</van-radio>
						</van-radio-group>
					</template>
				</van-field>
			</div>
			<div class="top10box">
			<van-field
				v-model="machineObj['remarks']"
				rows="2"
				autosize
				label="备注"
				type="textarea"
				maxlength="50"
				placeholder="请输入备注"
				show-word-limit
			/>
			</div>
		</van-cell-group>
	</div>
	<div class="btn-box"><van-button type="primary" color="#516df5" :style="{width:'90%'}" @click="machineClick">确认添加</van-button></div>
	<van-dialog v-model="dshow"  show-cancel-button @confirm="confirm" confirmButtonText="完成" confirmButtonColor="#80c269" cancelButtonColor="#ccc">
		<div class="show-cancel-box">
			<img src="~assets/images/common/machine.svg"/>
			<h3>新增机器成功</h3>
			<p>恭喜您新购了机器</p>
		</div>
	</van-dialog>


</div>
	
</template>

<script>
import { NavBar,Calendar,Cell,CellGroup,Field,Button,Dialog,DatetimePicker,Popup,Cascader,RadioGroup, Radio,Rate  } from 'vant';		//这个可以封装
import {addMachine} from 'network/machine'
export default {
  name: 'Machine',
	components:{
		[NavBar.name]:NavBar,
		[Cell .name]:Cell ,
		[Calendar .name]:Calendar ,
		[RadioGroup .name]:RadioGroup ,
		[Radio .name]:Radio ,
		[CellGroup  .name]:CellGroup  ,
		[Field   .name]:Field   ,
		[Button   .name]:Button   ,
		[Rate    .name]:Rate    ,
		[Popup   .name]:Popup   ,
		[Cascader    .name]:Cascader    ,
		[DatetimePicker   .name]:DatetimePicker   ,
		[Dialog.Component    .name]:Dialog.Component    ,
	},
	data(){
		return{
				// 选项列表，children 代表子选项，支持多级嵌套
			options: [
				{
					text: '广东省',
					value: '330000',
					children: [
						{ text: '广州市', value: '320100' },
						{ text: '肇庆市', value: '320200',children:[
							{ text: '高要区', value: '320300' },
						]},
					],
				},
				{
					text: '台湾省',
					value: '320000',
					children: [
						{ text: '高雄市', value: '320100' },
						{ text: '台北市', value: '320300' },
					],
				},
			],
			currentTime: '12:00',
			date: '',
			show: false,
			cshow: false,
			pshow: false,
			fieldValue:'',
			cascaderValue:'',
			dshow: false,
			machineObj:{
				"predictTime":'',
				"machinename":'',
				"machinenum":'',
				"remarks":'',
				"isuse":0,
				"datetext":0,
				"stars":5,
			}
		}
	},
	methods:{
		onClickRight(){
			this.$Toast('按钮')
		},
		onClickLeft(){
			this.$Toast('返回')
			this.$router.go(-1)
		},
		formatDate(date) {
			if(date.getDate()==13){
				this.$Toast('预约满了')
			}
			return `${date.getMonth() + 1}/${date.getDate()}`;
		},
		//日期确定
		onConfirm(date) {
			this.show = false;
			this.date = this.formatDate(date);
			this.machineObj['datetext']=date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()
			console.log(this.machineObj)
		},
		//添加确定按钮
		machineClick(){
			//次按钮点击需要对数据进行一下过滤-发送的时候拼接一下时间
			//console.log(this.machineObj)
			//这里发送数据
			addMachine(this.machineObj).then(res=>{
				if(res.status==0){
					this.dshow=true			//弹窗的
				}else{
					this.$Toast(res.message)
				}
			})
			console.log(this.machineObj)
		},
		confirm(){
			this.$router.push('cart').catch(res=>{res})
		}


	}
}
</script>

